.App {
  /* text-align: center; */
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  background: #303131;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

img{
  width: 400px;
}
ul {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}
ul li{
  margin: .3rem 1rem;
  cursor: pointer;
}